"use client";

import React, { useState, useEffect, useRef } from "react";
import NumberVisualizer from "./NumberVisualizer";
import FeedbackSuccess from "./FeedbackSuccess";
import FeedbackFailure from "./FeedbackFailure";
import LoudspeakerIcon from "./icons/LoudspeakerIcon";
import useGameLogic, { GameProblem } from "./useGameLogic";

// interface GameBoardMathAdditionProps {
//   // Props can be added here if needed in the future
// }
type GameBoardMathAdditionProps = Record<string, never>;

const GameBoardMathAddition: React.FC<GameBoardMathAdditionProps> = () => {
  const [userAnswer, setUserAnswer] = useState("");
  const inputRef = useRef<HTMLInputElement>(null);

  const generateProblem = (): GameProblem => {
    const newNum1 = Math.floor(Math.random() * 11);
    const newNum2 = Math.floor(Math.random() * (11 - newNum1));
    return { num1: newNum1, num2: newNum2, correctAnswer: newNum1 + newNum2 };
  };

  const checkAnswer = (
    answer: string,
    currentProblem: GameProblem
  ): boolean => {
    const parsedAnswer = parseInt(answer, 10);
    if (isNaN(parsedAnswer)) return false;
    return parsedAnswer === currentProblem.correctAnswer;
  };

  const {
    problem,
    showSuccess,
    showFailure,
    isMuted,
    feedbackText: hookFeedbackText,
    playInstructions,
    toggleMute,
    submitAnswer,
    startNewGame,
  } = useGameLogic({
    instructionText: "Add the numbers together!",
    successSoundFiles: Array.from(
      { length: 12 },
      (_, i) => `/sounds/success/success-${i + 1}.aac`
    ),
    failureSoundFiles: Array.from(
      { length: 9 },
      (_, i) => `/sounds/failure/failure-${i + 1}.aac`
    ),
    generateProblemFn: generateProblem,
    checkAnswerFn: checkAnswer,
    onCorrect: () => {
      // setUserAnswer(""); // Clear input on correct if desired, or hook can expose a resetInput callback
    },
    onIncorrect: () => {
      if (inputRef.current) {
        inputRef.current.select();
      }
    },
  });

  useEffect(() => {
    if (problem && !showSuccess && !showFailure && inputRef.current) {
      inputRef.current.focus();
    }
  }, [problem, showSuccess, showFailure]);

  const [displayFeedback, setDisplayFeedback] = useState("");

  useEffect(() => {
    setDisplayFeedback(hookFeedbackText);
  }, [hookFeedbackText]);

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    let value = event.target.value;
    if (value.length > 2) value = value.slice(0, 2);
    setUserAnswer(value);
    if (displayFeedback) setDisplayFeedback("");
  };

  const handleSubmit = () => {
    if (userAnswer.trim() === "") {
      setDisplayFeedback("Please enter a number.");
      return;
    }
    submitAnswer(userAnswer);
  };

  const handleKeyDown = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === "Enter") {
      handleSubmit();
    }
  };

  const isFeedbackShowing = showSuccess || showFailure;

  if (!problem) {
    return <div>Loading game...</div>;
  }

  const { num1, num2 } = problem;

  return (
    <div className="p-8 bg-neutral-800 rounded-xl shadow-2xl text-center max-w-3xl mx-auto border-2 border-teal-500 relative">
      <div className="absolute top-4 flex space-x-2">
        <button
          onClick={playInstructions}
          title="Play Instructions"
          className="p-2 text-sky-300 bg-zinc-700 hover:bg-zinc-600 rounded-md focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-neutral-800 focus:ring-sky-500 transition-colors"
        >
          <LoudspeakerIcon className="w-5 h-5" />
        </button>
        <button
          onClick={toggleMute}
          className="px-3 py-1.5 text-sm font-medium text-sky-300 bg-zinc-700 hover:bg-zinc-600 rounded-md focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-neutral-800 focus:ring-sky-500 transition-colors"
        >
          {isMuted ? "Unmute" : "Mute"}
        </button>
      </div>

      {showSuccess && (
        <div className="absolute inset-0 flex flex-col items-center justify-center bg-neutral-800 bg-opacity-95 z-10 rounded-xl">
          <FeedbackSuccess className="animate-bounce-gentle" />
          <p className="text-4xl font-bold text-green-500 mt-4">Correct!</p>
        </div>
      )}
      {showFailure && (
        <div className="absolute inset-0 flex flex-col items-center justify-center bg-neutral-800 bg-opacity-95 z-10 rounded-xl">
          <FeedbackFailure className="" />
          <p className="text-4xl font-bold text-red-500 mt-4">
            {displayFeedback || "Try again!"}
          </p>
        </div>
      )}

      <div
        className={`mb-8 ${
          isFeedbackShowing ? "opacity-0" : "opacity-100"
        } transition-opacity duration-300`}
      >
        <div className="flex justify-between items-center font-mono font-bold">
          <div className="flex-1 text-center text-sky-400 text-9xl">
            <span>{num1}</span>
          </div>
          <div className="flex-none px-3 md:px-4 text-neutral-400 text-6xl">
            <span>+</span>
          </div>
          <div className="flex-1 text-center text-sky-400 text-9xl">
            <span>{num2}</span>
          </div>
          <div className="flex-none px-3 md:px-4 text-neutral-400 text-6xl">
            <span>=</span>
          </div>
          <div className="flex-1 flex justify-center">
            <input
              ref={inputRef}
              type="number"
              value={userAnswer}
              onChange={handleInputChange}
              onKeyDown={handleKeyDown}
              disabled={isFeedbackShowing}
              className="w-full max-w-[200px] text-9xl font-mono font-bold text-sky-400 bg-transparent border-b-4 border-sky-500 focus:border-sky-300 outline-none text-center appearance-none m-0 p-0"
              aria-label="Enter sum"
            />
          </div>
        </div>

        <div className="mt-3 flex justify-between items-start min-h-[80px] md:min-h-[100px]">
          <div className="flex-1 flex justify-center">
            <NumberVisualizer count={num1 ?? 0} circleColor="bg-pink-500" />
          </div>
          <div className="flex-none px-3 md:px-4"> </div>
          <div className="flex-1 flex justify-center">
            <NumberVisualizer count={num2 ?? 0} circleColor="bg-indigo-600" />
          </div>
          <div className="flex-none px-3 md:px-4"> </div>
          <div className="flex-1"> </div>
        </div>
      </div>

      {!isFeedbackShowing && displayFeedback && (
        <p className="text-xl text-yellow-400 mt-4">{displayFeedback}</p>
      )}
    </div>
  );
};

export default GameBoardMathAddition;
