"use client";

import { useState, useEffect, useRef, useCallback } from "react";

// Types for the hook
export interface GameProblem {
  // Example: For math games
  num1?: number;
  num2?: number;
  // Example: For letter games
  letter1?: string;
  letter2?: string;
  correctLetter?: string;
  promptSound?: string; // Path to a sound that presents the question
  // Add other game-specific problem data as needed
  [key: string]: any; // Allow for other properties
}

export interface UseGameLogicProps {
  instructionText: string;
  successSoundFiles: string[];
  failureSoundFiles?: string[];
  generateProblemFn: () => GameProblem;
  checkAnswerFn: (answer: any, problem: GameProblem) => boolean;
  getProblemPromptSoundFn?: (problem: GameProblem) => string | null; // Optional: if problem has a spoken/sound prompt
  onCorrect?: (problem: GameProblem) => void;
  onIncorrect?: (problem: GameProblem, answer: any) => void;
  successDuration?: number;
  failureDuration?: number;
}

export interface UseGameLogicReturn {
  problem: GameProblem | null;
  showSuccess: boolean;
  showFailure: boolean;
  isMuted: boolean;
  isGameActive: boolean;
  feedbackText: string;
  playInstructions: () => void;
  toggleMute: () => void;
  submitAnswer: (answer: any) => void;
  startNewGame: () => void;
  playProblemSound: () => void;
}

const useGameLogic = (props: UseGameLogicProps): UseGameLogicReturn => {
  const {
    instructionText,
    successSoundFiles,
    failureSoundFiles = [], // Default to empty array if not provided
    generateProblemFn,
    checkAnswerFn,
    getProblemPromptSoundFn,
    onCorrect,
    onIncorrect,
    successDuration = 3000,
    failureDuration = 2500,
  } = props;

  const [problem, setProblem] = useState<GameProblem | null>(null);
  const [showSuccess, setShowSuccess] = useState(false);
  const [showFailure, setShowFailure] = useState(false);
  const [isMuted, setIsMuted] = useState(false);
  const [isGameActive, setIsGameActive] = useState(true); // Primarily for LetterSoundMatch type games
  const [feedbackText, setFeedbackText] = useState("");

  const instructionUtteranceRef = useRef<SpeechSynthesisUtterance | null>(null);
  const currentAudioRef = useRef<HTMLAudioElement | null>(null); // For feedback sounds and problem prompts
  const successTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const failureTimeoutRef = useRef<NodeJS.Timeout | null>(null);

  const clearAllTimeouts = useCallback(() => {
    if (successTimeoutRef.current) clearTimeout(successTimeoutRef.current);
    if (failureTimeoutRef.current) clearTimeout(failureTimeoutRef.current);
  }, []);

  const playSound = useCallback(
    (soundSrc: string) => {
      if (typeof window !== "undefined" && window.speechSynthesis.speaking) {
        window.speechSynthesis.cancel();
      }
      if (isMuted && !getProblemPromptSoundFn) {
        // Don't mute problem prompt sounds if they are distinct from feedback
        // This logic might need refinement based on how essential problem sounds are vs feedback sounds
        return;
      }

      if (currentAudioRef.current) {
        currentAudioRef.current.pause();
        currentAudioRef.current.onended = null;
        currentAudioRef.current.onerror = null;
        currentAudioRef.current = null;
      }
      const audio = new Audio(soundSrc);
      currentAudioRef.current = audio;
      audio.play().catch((error) => {
        console.error(`Error playing sound ${soundSrc}:`, error);
        if (currentAudioRef.current === audio) currentAudioRef.current = null;
      });
      audio.onended = () => {
        if (currentAudioRef.current === audio) currentAudioRef.current = null;
      };
      audio.onerror = (event) => {
        console.error(`Audio element error for ${soundSrc}:`, event);
        if (currentAudioRef.current === audio) currentAudioRef.current = null;
      };
    },
    [isMuted, getProblemPromptSoundFn]
  );

  const playInstructions = useCallback(() => {
    if (typeof window !== "undefined" && window.speechSynthesis) {
      window.speechSynthesis.cancel();
      const utterance = new SpeechSynthesisUtterance(instructionText);
      instructionUtteranceRef.current = utterance;
      // Voice selection logic (can be further customized or simplified)
      const voices = window.speechSynthesis.getVoices();
      let preferredVoice = voices.find(
        (v) => v.lang === "en-US" && v.name.toLowerCase().includes("female")
      );
      if (!preferredVoice)
        preferredVoice = voices.find((v) => v.lang === "en-US");
      if (!preferredVoice)
        preferredVoice = voices.find((v) => v.lang.startsWith("en"));
      if (preferredVoice) utterance.voice = preferredVoice;
      utterance.pitch = 1;
      utterance.rate = 1;
      utterance.onend = () => {
        instructionUtteranceRef.current = null;
      };
      utterance.onerror = () => {
        instructionUtteranceRef.current = null;
      };
      window.speechSynthesis.speak(utterance);
    }
  }, [instructionText]);

  const playProblemSound = useCallback(() => {
    if (problem && getProblemPromptSoundFn) {
      const soundPath = getProblemPromptSoundFn(problem);
      if (soundPath) {
        playSound(soundPath);
      }
    }
  }, [problem, getProblemPromptSoundFn, playSound]);

  const startNewGame = useCallback(() => {
    clearAllTimeouts();
    setShowSuccess(false);
    setShowFailure(false);
    setIsGameActive(true);
    setFeedbackText("");
    const newProblem = generateProblemFn();
    setProblem(newProblem);

    // Automatically play problem sound if applicable, with a slight delay
    if (getProblemPromptSoundFn && getProblemPromptSoundFn(newProblem)) {
      setTimeout(() => {
        if (problem === newProblem) {
          // ensure problem hasn't changed again quickly
          playProblemSound();
        }
      }, 100); // Small delay like in original components
    }
  }, [
    generateProblemFn,
    clearAllTimeouts,
    getProblemPromptSoundFn,
    playProblemSound,
  ]);

  useEffect(() => {
    startNewGame();
    return () => {
      clearAllTimeouts();
      if (currentAudioRef.current) currentAudioRef.current.pause();
      if (typeof window !== "undefined" && window.speechSynthesis.speaking) {
        window.speechSynthesis.cancel();
      }
    };
  }, [startNewGame]); // CAREFUL: ensure startNewGame dependencies are stable or memoized properly if it causes loops

  const submitAnswer = useCallback(
    (answer: any) => {
      if (!isGameActive || !problem) return;

      if (typeof window !== "undefined" && window.speechSynthesis.speaking) {
        window.speechSynthesis.cancel();
      }
      setIsGameActive(false); // Deactivate game while checking/showing feedback

      const isCorrect = checkAnswerFn(answer, problem);

      if (isCorrect) {
        setFeedbackText("Correct!"); // Or use a prop for custom message
        setShowSuccess(true);
        if (successSoundFiles.length > 0) {
          const randomSuccessSound =
            successSoundFiles[
              Math.floor(Math.random() * successSoundFiles.length)
            ];
          playSound(randomSuccessSound);
        }
        if (onCorrect) onCorrect(problem);

        successTimeoutRef.current = setTimeout(() => {
          startNewGame();
        }, successDuration);
      } else {
        setFeedbackText("Try again!"); // Or use a prop
        setShowFailure(true);
        if (failureSoundFiles && failureSoundFiles.length > 0) {
          const randomFailureSound =
            failureSoundFiles[
              Math.floor(Math.random() * failureSoundFiles.length)
            ];
          playSound(randomFailureSound);
        }
        if (onIncorrect) onIncorrect(problem, answer);

        failureTimeoutRef.current = setTimeout(() => {
          setShowFailure(false);
          setIsGameActive(true);
          setFeedbackText("");
          // Potentially refocus input or give another try without generating a new problem
          // This part might need to be handled by the component using the hook
        }, failureDuration);
      }
    },
    [
      isGameActive,
      problem,
      checkAnswerFn,
      successSoundFiles,
      failureSoundFiles,
      playSound,
      startNewGame,
      successDuration,
      failureDuration,
      onCorrect,
      onIncorrect,
    ]
  );

  const toggleMute = () => setIsMuted(!isMuted);

  return {
    problem,
    showSuccess,
    showFailure,
    isMuted,
    isGameActive,
    feedbackText,
    playInstructions,
    toggleMute,
    submitAnswer,
    startNewGame,
    playProblemSound,
  };
};

export default useGameLogic;
